import React from 'react';
import AddIcon from '@material-ui/icons/Add';
import RefreshIcon from '@material-ui/icons/Refresh';
import RemoveIcon from '@material-ui/icons/Remove';
import {IconButton, Badge, Grid} from '@material-ui/core';
import { blue } from '@material-ui/core/colors';
import PropTypes from 'prop-types';
import useStyles from 'components/useStyles'
import ExtractData from './ExtractData'
import CardWeather from './CardWeather'
import SimpleDialog from './SimpleDialog'


const ShowCities=props=>{
	const classes=useStyles()
	const { cityList, setCityList, handleClickOpen, handleClose, saveLocationList, open,  selectedValue, data
			} = props

	const removeLocation=(id)=>{
		let newCityList=cityList
		if (newCityList[id]) {
			delete newCityList[id];
		saveLocationList(newCityList);
		setCityList(newCityList)
		}
		window.location.reload(false);
	}

	const getCardData=
			data.map((cardCity)=>{
				let showData=ExtractData(cardCity.data)
				return ({
					[cardCity.data.city.id]:showData
				}
				)
			}
			)

return (

   <div className={classes.root} key='showCity'>
	<Grid  container spacing={2}   direction="row"  justify="center" alignItems="center">
	<IconButton  id="butAdd"  style={{ color: blue[50] }} className={classes.fab} onClick={handleClickOpen}>
        <Badge badgeContent={Object.keys(cityList).length} color="secondary">
            <AddIcon fontSize='large'/>
        </Badge>
    </IconButton>
	<SimpleDialog selectedValue={selectedValue} open={open} onClose={handleClose}  />
			   {getCardData.map((cardCity)=>{
				   return (
						<Grid item xs={12}  sm={12} key={Object.keys(cardCity)[0]}>
							<CardWeather id={Object.keys(cardCity)[0]} showData={cardCity[Object.keys(cardCity)[0]]} removeLocation={removeLocation}/>
						</Grid>	
					)				 
				   })}
	
	</Grid>
		
	</div>

)
}

ShowCities.propTypes = {
  cityList: PropTypes.object.isRequired,
  setCityList: PropTypes.func.isRequired,
  handleClickOpen: PropTypes.func.isRequired,
  handleClose: PropTypes.func.isRequired,
  saveLocationList: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  selectedValue: PropTypes.string.isRequired,
  data: PropTypes.array.isRequired
};

export default ShowCities