import React from 'react';
import PropTypes from 'prop-types';

import Avatar from '@material-ui/core/Avatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import ListItemText from '@material-ui/core/ListItemText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import PersonIcon from '@material-ui/icons/Person';
import AddIcon from '@material-ui/icons/Add';
import useStyles from 'components/useStyles';

const  SimpleDialog=(props)=> {
  const classes = useStyles();
  const { onClose, selectedValue, open } = props;
  const cities=[
	{"id": "6554291","name":"Trier","state":"","country":"DE"}, 
	{"id" : "1151254","name" : "Phuket","state" : "","country" : "TH"},
	{"id" : "1609348","name" : "Bangkok", "state" : "", "country" : "TH"},
	{"id" : "5128638","name" : "New York","state" : "NY","country" : "US"},
	{"id" : "1850147","name" : "Tokyo","state" : "","country" : "JP"},
	{"id" : "1816670","name" : "Beijing","state" : "","country" : "CN"},
	{"id" : "6455259","name" : "Paris","state" : "","country" : "FR"},
	{"id" : "2643743","name" : "London","state" : "","country" : "GB"}
	]

  const handleClose = () => {
    onClose(selectedValue);
  };

  const handleListItemClick = (value) => {
    onClose(value);
  };

  return (
  <React.Fragment>
    <Dialog onClose={handleClose} aria-labelledby="simple-dialog-title" open={open}>
      <DialogTitle id="simple-dialog-title">Select City</DialogTitle>
      <List>
        {cities.map((city) => (
          <ListItem button onClick={() => handleListItemClick(city.id)} key={city.id}>
            <ListItemAvatar>
              <Avatar className={classes.avatar}>
                <PersonIcon />
              </Avatar>
            </ListItemAvatar>
            <ListItemText primary={city.name} />
          </ListItem>
        ))}

        <ListItem autoFocus button onClick={() => handleListItemClick('addAccount')}>
          <ListItemAvatar>
            <Avatar>
              <AddIcon />
            </Avatar>
          </ListItemAvatar>
          <ListItemText primary="Add account" />
        </ListItem>
      </List>
    </Dialog>
	</React.Fragment>
  );
}

SimpleDialog.propTypes = {
  onClose: PropTypes.func.isRequired,
  open: PropTypes.bool.isRequired,
  selectedValue: PropTypes.string.isRequired,
};

export default SimpleDialog