import React from 'react';
import { Card, CardContent, Grid, Typography, IconButton } from '@material-ui/core';

import PropTypes from 'prop-types';
import useStyles from 'components/useStyles'
import RefreshIcon from '@material-ui/icons/Refresh';
import RemoveIcon from '@material-ui/icons/Remove';
import { blue } from '@material-ui/core/colors';
import clearDay from 'images/clearDay.svg';
import clearNight from 'images/clearNight.svg';
import cloudy from 'images/cloudy.svg';
import fog from 'images/fog.svg';
import partlyCloudyDay from 'images/partlyCloudyDay.svg';
import partlyCloudyNight from 'images/partlyCloudyNight.svg';
import rain from 'images/rain.svg';
import sleet from 'images/sleet.svg';
import snow from 'images/snow.svg';
import thunderstorm from 'images/thunderstorm.svg';
import tornado from 'images/tornado.svg';
import wind from 'images/wind.svg';

const _ = require("lodash");

const CardWeather=props=>{
	const { id, showData, removeLocation } = props
	const classes = useStyles()
	const cityInfo=showData.cityInfo
	const tempMax=showData.tempMaxDate
	const tempMin=showData.tempMinDate
	const iconDate=showData.iconShowDate
	const iconMain=iconDate[Object.keys(iconDate)[0]]
	
	return (
	<Card>
		<CardContent>
		<Grid container  
			direction="row"
			spacing={2}
			justify="space-between"
			alignItems="flex-start"
		>
		<Grid item >
			<IconButton  id="butRefresh"  style={{ color: blue[500] }} edge='start' onClick= {(event)=>window.location.reload(false)} >
				<RefreshIcon fontSize='large'/>
			</IconButton>
		</Grid>
		<Grid item>
			<IconButton aria-label="remove" color="secondary" className={classes.removeCity}  onClick={(e)=>removeLocation(id)}>
				<RemoveIcon fontSize='large'/>
			</IconButton>
		</Grid>
		</Grid>
		<Grid container  
			direction="row"
			spacing={2}
			justify="space-between"
			alignItems="flex-start"
		>		
		<Grid item>
			<Typography variant="h4">
				{cityInfo.nameCity}, {cityInfo.countryCity}
			</Typography>
			<Typography variant="h6">
				{cityInfo.timeCity}
			</Typography>
			<Typography variant="h6">
				{cityInfo.desc}
			</Typography>
		</Grid>
		<Grid item>
			<Typography variant="h3">
				{cityInfo.tempC.toFixed(2)}째C
			</Typography>
			<img  width='70%' src={iconMain}  padding='50px' alt="clear day" />
		</Grid>
		<Grid item>
			<Typography variant="h6">
				Wind: {cityInfo.windSpeed} mph {cityInfo.windDeg}째
			</Typography>
			<Typography variant="h6">
				Sunrise {cityInfo.sunRise}
			</Typography>	
			<Typography variant="h6">
				Sunset {cityInfo.sunSet}
			</Typography>
		</Grid>
		<Grid item>
		
		</Grid>
		<Grid container  
			direction="row"
			spacing={2}
			justify="space-between"
			alignItems="flex-start"
		>		
		{Object.keys(tempMax).map((dayWeather)=>{return(
		<Grid item key={dayWeather+"1"}>
				<Typography >
					{dayWeather}
				</Typography>
				<img  width='50%' src={_.get(iconDate,[dayWeather])}  padding='50px' alt="clear day" />
				<Typography>
					{Number(tempMax[dayWeather]).toFixed(2)}째C
				</Typography>
				<Typography>
					{Number(tempMin[dayWeather]).toFixed(2)}째C
				</Typography>

			</Grid>		
			)
			})
		}
		</Grid>
		</Grid>
		</CardContent>
	</Card>
	)	
}

CardWeather.propTypes = {
  showData: PropTypes.object,
  id: PropTypes.string,
  removeLocation: PropTypes.func
};

export default CardWeather