import * as d3 from "d3";
import clearDay from 'images/clearDay.svg';
import clearNight from 'images/clearNight.svg';
import cloudy from 'images/cloudy.svg';
import fog from 'images/fog.svg';
import partlyCloudyDay from 'images/partlyCloudyDay.svg';
import partlyCloudyNight from 'images/partlyCloudyNight.svg';
import rain from 'images/rain.svg';
import sleet from 'images/sleet.svg';
import snow from 'images/snow.svg';
import thunderstorm from 'images/thunderstorm.svg';
import tornado from 'images/tornado.svg';
import wind from 'images/wind.svg';

const _ = require("lodash");

const ExtractData=(data)=>{
	let thisList=data?_.get(data,"list"):[]

	let weekdays = new Array(
        "Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"
    );
	let cityInfo={}
	cityInfo.nameCity=_.get(data, 'city.name')	
	cityInfo.countryCity=_.get(data, 'city.country')	
	let rDate=new Date(_.get(data,'city.sunrise')*1000)
	cityInfo.sunRise=rDate.toLocaleTimeString()
	let sDate=new Date(_.get(data,'city.sunset')*1000)
	cityInfo.sunSet=sDate.toLocaleTimeString()
	let date=new Date(_.get(data,'list[0].dt')*1000)
	cityInfo.timeCity=date.toUTCString()
	cityInfo.tempMax=_.get(data,'list[0].main.temp_max')-273.1
	cityInfo.tempMin=_.get(data,'list[0].main.temp_min')-273.1
	cityInfo.tempC=_.get(data,'list[0].main.temp')-273.1
	cityInfo.humidity=_.get(data,'list[0].main.humidity')
	cityInfo.desc=_.get(data,'list[0].weather[0].description')
	cityInfo.descM=_.get(data,'list[0].weather[0].main')
	cityInfo.windDeg=_.get(data,'list[0].wind.deg')
	cityInfo.windSpeed=_.get(data,'list[0].wind.speed')
	
	const iconShow=[
		{iconId:'01d', iconShow:clearDay},
		{iconId:'02d', iconShow:partlyCloudyDay},
		{iconId:'03d', iconShow:cloudy},
		{iconId:'04d', iconShow:cloudy},
		{iconId:'09d', iconShow:rain},
		{iconId:'10d', iconShow:rain},
		{iconId:'11d', iconShow:thunderstorm},
		{iconId:'13d', iconShow:snow},
		{iconId:'50d', iconShow:fog},
		{iconId:'01n', iconShow:clearDay},
		{iconId:'02n', iconShow:partlyCloudyDay},
		{iconId:'03n', iconShow:cloudy},
		{iconId:'04n', iconShow:cloudy},
		{iconId:'09n', iconShow:rain},
		{iconId:'10n', iconShow:rain},
		{iconId:'11n', iconShow:thunderstorm},
		{iconId:'13n', iconShow:snow},
		{iconId:'50n', iconShow:fog}
	]
	let newList=thisList?thisList.map((eachList, key)=>{
		let dateShow=new Date(eachList.dt*1000)
		return ( {
			dt:dateShow.getDate(),
			dayShow: weekdays[dateShow.getDay()],
			temp:eachList.main.temp,
			tempMax:eachList.main.temp_max,
			tempMin:eachList.main.temp_min,
			description:eachList.weather[0].description,
			theIcon:_.get(_.filter(iconShow, {iconId: eachList.weather[0].icon})[0],'iconShow')
		})
	}):[]
	const  tempMaxDate = d3.nest()
	  .key(function(d) { return d.dayShow})
	  .rollup(function(v) { return d3.max(v, function(d) { return d.tempMax-273.1; })
		}).object(newList);

	const  tempMinDate = d3.nest()
	  .key(function(d) { return d.dayShow})
	  .rollup(function(v) { return d3.min(v, function(d) { return d.tempMin-273.1; })
		}).object(newList);

	const  iconShowDate = d3.nest()
	  .key(function(d) { return d.dayShow})
	  .rollup(function(v) { return d3.max(v, function(d) { return d.theIcon; })
		}).object(newList);   
	
return {tempMaxDate, tempMinDate, iconShowDate, cityInfo}
}

export default ExtractData